import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { LineAddOptionsConstants } from '../shared/constants/line-add-options.constants';
import { AbstractBaseComponent, LoaderService, UrlService } from '@telenet/ng-lib-page';
import isEmpty from 'lodash-es/isEmpty';
import first from 'lodash-es/first';
import uniq from 'lodash-es/uniq';
import { of } from 'rxjs';
import { finalize, map } from 'rxjs/operators';
import { MessageService, MessagesModule } from '@telenet/ng-lib-message';
import cloneDeep from 'lodash-es/cloneDeep';
import { EligibleProductInterface } from '../../../shared/common/interfaces/products/eligible-product.interface';
import { LineChangeTariffPlanConstants } from '../shared/constants/line-change-tariff-plan.constants';
import { LineAddressDetailsConstants } from '../../../shared/common/constants/line-address-details.constants';
import { EligibleProductsService } from '../../../shared/common/services/products/eligible-products.service';
import { ProductDatalayerService } from '../shared/services/datalayer/product-datalayer.service';
import { FleetMessageService } from '../shared/services/message/fleet-message.service';
import { FleetManagerAvailableOptionComponent } from './views/available-option/fleet-manager-available-option.component';
import { TranslateModule } from '@ngx-translate/core';
import { FleetManagerStickyActionsComponent } from './views/sticky-actions/fleet-manager-sticky-actions.component';
import { FleetManagerLineItemComponent } from './views/line-item/fleet-manager-line-item.component';
import { FleetManagerWarningMessageComponent } from './views/warning-message/fleet-manager-warning-message.component';
import { CommonModule, NgClass } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { CustomerProductHolding } from '../shared/models/customer-product-holding.model';
import { SalesNgrxModule } from '../shared/store/sales/state/sales/sales-ngrx.module';
import { ProductSelectedLineService } from '../shared/services/product/product-selected-line-service';
import { ProductOptionsMapService } from '../shared/services/product/product-options-map.service';

@Component({
  selector: 'tg-fleet-manager-line-add-options',
  templateUrl: './fleet-manager-line-add-options.component.html',
  standalone: true,
  styles: [],
  imports: [
    SalesNgrxModule,
    FormsModule,
    CommonModule,
    MessagesModule,
    FleetManagerAvailableOptionComponent,
    FleetManagerStickyActionsComponent,
    FleetManagerWarningMessageComponent,
    FleetManagerLineItemComponent,
    TranslateModule,
    NgClass,
  ],
})
export class FleetManagerLineAddOptionsComponent extends AbstractBaseComponent implements OnInit {
  @Input() msisdn: string;
  @Input() messageGroup: string = LineAddOptionsConstants.MESSAGE_GROUP;
  @Input() toggleClass: boolean;

  @Output() closeOverlay: EventEmitter<void> = new EventEmitter<void>();

  public productIdentifier: string;
  public salesOrderResponse;
  public category;
  public stickyContainer = false;
  public selectedLines: CustomerProductHolding[] = [];
  public initialSelectedLines: CustomerProductHolding[] = [];
  public showFilteredLines = false;
  public productLineOptions = [];
  public initialLineOptions = [];
  public toggleContainer = false;
  public isShowWarning = false;
  public optionProducts = [];

  constructor(
    private readonly urlService: UrlService,
    private readonly messageService: MessageService,
    private readonly eligibleProductService: EligibleProductsService,
    private readonly productSelectedLineService: ProductSelectedLineService,
    private readonly productOptionsMapService: ProductOptionsMapService,
    private readonly productDataLayerService: ProductDatalayerService,
    private readonly loaderService: LoaderService,
    private readonly fleetMessageService: FleetMessageService
  ) {
    super();
  }

  ngOnInit(): void {
    if (isEmpty(this.msisdn)) {
      this.productIdentifier = this.urlService.getParameterByName(LineAddressDetailsConstants.MSISDN);
      if (isEmpty(this.productIdentifier)) {
        this.productSelectedLineService.getSelectedMsisdn().subscribe((selectedMsisdn) => {
          this.productIdentifier = selectedMsisdn;
        });
      }
    } else {
      this.productIdentifier = this.msisdn;
    }

    if (this.productIdentifier) {
      this.productDataLayerService.pushPageStepDataToAnalytics(
        LineAddOptionsConstants.STEP_ID_ADD_OPTION_OVERLAY,
        LineChangeTariffPlanConstants.EVENT_ATTRIBUTE_INTENT_CHANGE_TARIFF_PLAN
      );
      this.productDataLayerService.addFlowStartEventToUDL(
        LineAddOptionsConstants.EVENT_NAME_CHANGE_LINE_DETAILS_STARTED,
        this.salesOrderResponse,
        undefined,
        undefined
      );
      this.messageService.clearMessages(this.messageGroup);
      this.productSelectedLineService.getSelectedLines().subscribe((selectedLines) => {
        this.initialSelectedLines = this.productOptionsMapService.mapOptionCategory(selectedLines);
        this.selectedLines = cloneDeep(this.initialSelectedLines);
        this.getLineOptions();
      });
    }
  }

  getLineOptions(): void {
    this.loaderService.start();
    this.eligibleProductService
      .getEligibleProductsByIdentifier(LineAddOptionsConstants.MESSAGE_GROUP, this.getFirstSelectedLineId())
      .pipe(finalize(() => this.loaderService.stop()))
      .subscribe({
        next: (response: EligibleProductInterface[]) => {
          if (!isEmpty(response)) {
            this.productLineOptions = this.productOptionsMapService.groupOptionsByCategory(response);
            this.optionProducts = first(response).optionProducts;
            const mutuallyExclusiveSubcategory = this.loadMutuallyExclusiveSubcategory(first(response).optionProducts);
            const options = this.getOptions(this.selectedLines);
            const lineOptions = this.getProductLineOptions();
            lineOptions.forEach((lineOption) => {
              const filteredOptions = this.isOptionActive(options, lineOption);
              if (mutuallyExclusiveSubcategory.includes(lineOption.category)) {
                lineOption.isDisabled = true;
              }
              if (filteredOptions.length === this.selectedLines.length) {
                lineOption.isSelected = true;
                lineOption.isDisabled = true;
              }
            });
            this.initialLineOptions = this.productLineOptions;
          } else {
            this.closeOverLay();
          }
        },
        error: (error) => {
          this.fleetMessageService.showError(error, this.messageGroup);
          this.closeOverLay();
        },
      });
  }

  isOptionActive(options, lineOption) {
    return options.filter(
      (option) =>
        option.productId === lineOption.id &&
        (option.status === LineAddOptionsConstants.STATUS_ACTIVE ||
          option.status === LineAddOptionsConstants.ACTIVATION_PASSED_PONR)
    );
  }

  getProductLineOptions() {
    const options = [];
    this.productLineOptions.forEach((productLineOption) => {
      productLineOption.subcategory.forEach((subcategory) => {
        options.push(subcategory);
      });
    });
    return options;
  }

  private loadMutuallyExclusiveSubcategory(response) {
    const category = [];
    of(response).pipe(
      map((data) => {
        data
          .filter(function (currentObject) {
            return (
              currentObject.productInfo &&
              currentObject.productInfo.subcategory !== undefined &&
              currentObject.productInfo.subcategory.mutualExclusion === true
            );
          })
          .groupBy(function (currentObject) {
            return currentObject.productInfo.subcategory.localizedName;
          })
          .map(function (value, key) {
            value.map((item) => {
              if (item.type === LineAddOptionsConstants.MANDATORY) {
                category.push(key);
              }
            });
          });
      })
    );
    return uniq(category);
  }

  getFirstSelectedLineId() {
    return this.selectedLines.length > 0 ? this.selectedLines[0].identifier : '';
  }

  public hasAffectedLine(): boolean {
    const affectedLine = this.selectedLines.filter((selectedLine) => {
      return selectedLine.isAffected;
    });
    if (!affectedLine) {
      this.showFilteredLines = false;
    }
    return !isEmpty(affectedLine);
  }

  public toggleStickyContainer(): void {
    this.stickyContainer = !this.stickyContainer;
  }

  public revertChanges(): void {
    this.selectedLines = cloneDeep(this.initialSelectedLines);
    this.productLineOptions = cloneDeep(this.initialLineOptions);
    this.toggleContainer = false;
  }

  public showWarningContent(): void {
    this.isShowWarning = true;
  }

  public getOptions(selectedLines) {
    const options = [];
    selectedLines.forEach((selectedLine) => {
      selectedLine.options.forEach((option) => {
        options.push(option);
      });
    });
    return options;
  }

  public toggleContainerData(toggleContainer): void {
    this.toggleContainer = toggleContainer;
  }

  public closeOverLay(): void {
    this.closeOverlay.emit();
  }
}
