import { Component, EventEmitter, Input, Output } from '@angular/core';
import cloneDeep from 'lodash-es/cloneDeep';
import flatMap from 'lodash-es/flatMap';
import isEqual from 'lodash-es/isEqual';
import sortBy from 'lodash-es/sortBy';
import { LineAddOptionsConstants } from '../../../shared/constants/line-add-options.constants';
import { TranslateModule } from '@ngx-translate/core';
import { CommonModule, NgClass } from '@angular/common';

@Component({
  selector: 'tg-fleet-manager-available-option',
  standalone: true,
  templateUrl: './fleet-manager-available-option.component.html',
  imports: [TranslateModule, CommonModule, NgClass],
})
export class FleetManagerAvailableOptionComponent {
  @Input() productLineOptions = [];
  @Input() selectedLines = [];

  @Output() toggleContainerData: EventEmitter<boolean> = new EventEmitter<boolean>();

  toggleContainer = false;

  toggleCategories(categories): void {
    const activeCategory = this.getItemsByType(this.productLineOptions, 'isOpen', true);
    if (activeCategory) {
      activeCategory.isOpen = false;
    }
    if (!isEqual(activeCategory, categories)) {
      categories.isOpen = !categories.isOpen;
    }
  }

  addOption(selectedOption): void {
    this.selectedLines.forEach((selectedLine) => {
      const inactiveOption = this.getOption(
        selectedLine.options,
        selectedOption.category,
        LineAddOptionsConstants.STATUS_INACTIVE
      );
      const hasSameOption = inactiveOption && inactiveOption.id === selectedOption.id;
      const optionToBeReplaced = hasSameOption ? selectedOption : inactiveOption;
      const activeOption = this.getOption(
        selectedLine.options,
        selectedOption.category,
        LineAddOptionsConstants.STATUS_ACTIVE
      );
      const hasSameActiveOption = activeOption && activeOption.productId === selectedOption.id;

      if (activeOption) {
        activeOption.optionCategory = '';
      }

      this.optionToBeReplaced(optionToBeReplaced, selectedLine, selectedOption);

      if (!hasSameOption && !hasSameActiveOption) {
        this.updateSelection(selectedLine, selectedOption, activeOption, inactiveOption);
        this.toggleContainer = true;
        this.updateDisabledOption(selectedOption, true);
      }

      this.displayLabel(selectedLine);
      this.toggleContainerValue();
    });
  }

  getItemsByType(list, property, value) {
    return list.find((option) => option[property] === value);
  }

  removeOption(options, optionItem): void {
    const index = options.findIndex(
      (option) => option.id === optionItem.id && option.status === LineAddOptionsConstants.STATUS_INACTIVE
    );

    this.getItemsByType(
      this.getItemsByType(this.productLineOptions, 'category', optionItem.category).subcategory,
      'id',
      optionItem.id
    ).isSelected = false;
    options.splice(index, 1);
  }

  private optionToBeReplaced(optionToBeReplaced: boolean, selectedLine, selectedOption): void {
    if (optionToBeReplaced) {
      this.removeOption(selectedLine.options, optionToBeReplaced);
      selectedLine.isAffected = this.isAffected(selectedLine.options);
      this.updateDisabledOption(selectedOption, false);
    }
  }

  private updateSelection(selectedLine, selectedOption, activeOption, inactiveOption): void {
    selectedOption.isSelected = true;
    selectedOption.optionCategory =
      activeOption ||
      (inactiveOption && inactiveOption.optionCategory === LineAddOptionsConstants.OPTION_CATEGORY_UPGRADED)
        ? LineAddOptionsConstants.OPTION_CATEGORY_UPGRADED
        : LineAddOptionsConstants.OPTION_CATEGORY_ADDED;
    selectedOption.status = LineAddOptionsConstants.STATUS_INACTIVE;

    if (activeOption) {
      selectedOption.upgradedFrom = activeOption.volume;
      activeOption.optionCategory =
        activeOption.category === selectedOption.category ? LineAddOptionsConstants.OPTION_CATEGORY_REPLACED : '';
    }

    selectedLine.options = sortBy(selectedLine.options, LineAddOptionsConstants.GROUP_BY_KEY);
    selectedLine.options.push(cloneDeep(selectedOption));
    selectedLine.isAffected = this.isAffected(selectedLine.options);
  }

  private getOption(options, categoryName, status) {
    return options.find((option) => option.category === categoryName && option.status === status);
  }

  private displayLabel(selectedLine): void {
    selectedLine.isUpgraded = this.hasItemWithOptionCategoryUpgraded(selectedLine.options);
    selectedLine.isAdded = this.hasItemWithOptionCategoryAdded(selectedLine.options);
  }

  private updateDisabledOption(selectedOption, isDisabled): void {
    const disabledOption = this.getOptionBasedOnStatus(selectedOption, isDisabled);
    if (disabledOption) {
      disabledOption.isSelected = !isDisabled;
      disabledOption.isDisabled = !isDisabled;
    }
  }

  private getOptionBasedOnStatus(selectedOption, isDisabled) {
    const productOptions = this.getProductLineOptions();
    return productOptions.find((productOption) => {
      return productOption.category === selectedOption.category && productOption.isDisabled === isDisabled;
    });
  }

  private getProductLineOptions() {
    return flatMap(this.productLineOptions, (option) => option.subcategory);
  }

  private isAffected(options): boolean {
    return this.hasItemWithOptionCategoryAdded(options) || this.hasItemWithOptionCategoryUpgraded(options);
  }

  private hasItemWithOptionCategoryUpgraded(options): boolean {
    return this.hasItemWithOptionOptionCategory(options, LineAddOptionsConstants.OPTION_CATEGORY_UPGRADED);
  }

  private hasItemWithOptionCategoryAdded(options): boolean {
    return this.hasItemWithOptionOptionCategory(options, LineAddOptionsConstants.OPTION_CATEGORY_ADDED);
  }

  private hasItemWithOptionOptionCategory(options, optionCategory: string): boolean {
    return !!this.getItemsByType(options, LineAddOptionsConstants.OPTION_CATEGORY, optionCategory);
  }

  private toggleContainerValue(): void {
    this.toggleContainerData.emit(this.toggleContainer);
  }
}
