import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import isEmpty from 'lodash-es/isEmpty';
import { OcapiCallConfig, OcapiService } from '@telenet/ng-lib-ocapi';
import { ProductSubscriptionsInterface } from '../interfaces/product-subscriptions.interface';
import { ProductPendingOperationsInterface } from '../interfaces/product-pending-operations.interface';
import { AbstractProductService } from './abstract/abstract-product-service';
import { CustomerProductHolding } from '../models/customer-product-holding.model';
import { CustomerProductHoldingsMapper } from '../mappers/customer-product-holdings.mapper';
import { ProductSubscriptionsMapper } from '../mappers/product-subscriptions.mapper';
import { ProductPendingOperationsCatalogMapper } from '../mappers/product-pending-operations.mapper';
import { CacheService } from '@telenet/ng-lib-page';
import { ProductConstants } from '../constants/product.constant';
import { HttpParamsUtil } from '../utils/http-params.util';
import { GetPendingProductsActionPayload } from '../interfaces/self-install.actions';

@Injectable({
  providedIn: 'root',
})
export class ProductService extends AbstractProductService {
  private allProductsCache: CustomerProductHolding[];
  private readonly queryParamList = ['producttypes', 'status'];

  constructor(
    private readonly ocapiService: OcapiService,
    private readonly customerProductHoldingsMapper: CustomerProductHoldingsMapper,
    private readonly productSubscriptionsMapper: ProductSubscriptionsMapper,
    private readonly productPendingOpearionsCatalogMapper: ProductPendingOperationsCatalogMapper,
    private readonly cacheService: CacheService
  ) {
    super();
  }

  getAllProducts(
    messageGroup: string,
    status?: string,
    force?: boolean,
    productType?: string
  ): Observable<CustomerProductHolding[]> {
    if (this.allProductsCache && !force) {
      return of(this.allProductsCache);
    }
    let endpoint = ProductConstants.EMPTY;
    const payload = {
      status: status,
      producttypes: productType,
    };
    endpoint = this.getServiceURI(
      this.computeEndpointFromPayload(endpoint, this.queryParamList, payload),
      ProductConstants.CONTEXT_PRODUCTS,
      ProductConstants.API_VERSION_V1
    );
    return this.ocapiService
      .doGet(new OcapiCallConfig(messageGroup, endpoint, this.customerProductHoldingsMapper))
      .pipe(
        map((response: CustomerProductHolding[]) => {
          if (!isEmpty(response)) {
            this.allProductsCache = response;
          }
          return response;
        })
      );
  }

  getProductLinesByType(
    messageGroup: string,
    type?: string,
    customerCategory?: string,
    status?: string,
    flow?: string
  ): Observable<ProductSubscriptionsInterface[]> {
    let endpoint = HttpParamsUtil.createEndpointWithQueryParams('/product-subscriptions', {
      producttypes: type,
      customerCategory: customerCategory,
      status: status,
      flow: flow,
    });

    endpoint = this.getServiceURI(endpoint, '', ProductConstants.API_VERSION_V1);
    if (!this.cacheService.get(endpoint)) {
      this.cacheService.add(
        endpoint,
        this.ocapiService.doGet(new OcapiCallConfig(messageGroup, endpoint, this.productSubscriptionsMapper))
      );
    }
    return this.cacheService.get(endpoint);
  }

  getPendingOperations(messageGroup: string): Observable<ProductPendingOperationsInterface[]> {
    const endpoint = this.getProductServiceURI(
      ProductConstants.API_VERSION_V1,
      ProductConstants.CONTEXT_PRODUCTS,
      ProductConstants.PENDING_OPERARIONS,
      ProductConstants.EMPTY,
      ProductConstants.EMPTY
    );
    return this.ocapiService.doGet(
      new OcapiCallConfig(messageGroup, endpoint, this.productPendingOpearionsCatalogMapper)
    );
  }

  getPendingProducts(payload: GetPendingProductsActionPayload): Observable<ProductSubscriptionsInterface[]> {
    let endpoint = `/pendingorders?customernumber=${payload.customerNumber}`;

    if (!isEmpty(payload.skipLocationCheck)) {
      endpoint = endpoint.concat(`&skipLocationCheck=${payload.skipLocationCheck}`);
    }

    endpoint = this.getServiceURI(endpoint, '', ProductConstants.API_VERSION_V1);
    return this.ocapiService
      .doGet(new OcapiCallConfig(payload.messageGroup, endpoint, this.productSubscriptionsMapper))
      .pipe(map((response: ProductSubscriptionsInterface[]) => response));
  }
}
