import { PriceAttributes, TaxTypeEnum, YUPUpfrontPaymentInterface } from '@telenet/ng-lib-form';

export class Prices {
  taxInclPrices: PriceAttributes;
  taxExclPrices: PriceAttributes;
  earlyTerminationFee: string;

  getOneTimeTotal(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].oneTimeTotal;
  }

  getOneTimeDiscount(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].oneTimeDiscount;
  }

  getOneTimeDiscounted(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].oneTimeDiscounted;
  }

  getOneTimeDiscountedOnNextInvoice(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].oneTimeDiscountedOnNextInvoice;
  }

  getRecurrentTotal(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].recurrentTotal;
  }

  getRecurrentDiscount(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].recurrentDiscount;
  }

  getRecurrentDiscounted(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].recurrentDiscounted;
  }

  getUpfrontTotal(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].upfrontTotal;
  }

  getDiscountPrice(taxType: TaxTypeEnum): string {
    return this[taxType] && this[taxType].discountPrice;
  }

  getYUPUpfrontPayment(taxType: TaxTypeEnum): YUPUpfrontPaymentInterface {
    return this[taxType] && this[taxType].yupUpfrontPayment;
  }
}
