import { CharsEnum, OmapiProduct } from '@telenet/ng-lib-omapi';
import { ExtendedUsageInterface } from '../interfaces/extended-usage.interface';
import { AllocatedUsageInterface } from '../interfaces/allocated-usage.interface';
import { BaseExtendedUsageInterface } from '../interfaces/base-extended-usage.interface';
import { TotalInternetUsageInterface } from '../interfaces/total-internet-usage.interface';
import { PeakUsageInterface } from '../interfaces/peak-usage.interface';

export class InternetUsage {
  category: string;
  productInfo: OmapiProduct;
  daysUntil: string;
  validity: string;
  allocatedUsage: AllocatedUsageInterface;
  extendedUsage: ExtendedUsageInterface;
  totalUsage: TotalInternetUsageInterface;
  peakUsage: PeakUsageInterface;
  baseExtendedUsage?: BaseExtendedUsageInterface[];
  usedBucketUsage?: number;
  totalBucketUsage?: number;
  usedBucketPercentage?: string;
  barColorClass?: string;

  isFUPLine(): boolean {
    return this.category.toUpperCase() === 'FUP';
  }

  isCAPLine(): boolean {
    return this.category.toUpperCase() === 'CAP';
  }

  isUnlimitedLine(): boolean {
    return this.category.toUpperCase() === 'UNLIMITED';
  }

  isBelowThreshold(usedUnits: number, limit: number): boolean {
    return usedUnits < limit;
  }

  isBetweenThresholds(usedUnits: number, lowerLimit: number, upperLimit: number): boolean {
    return usedUnits >= lowerLimit && usedUnits < upperLimit;
  }

  isAboveThreshold(usedUnits: number, limit: number): boolean {
    return usedUnits >= limit;
  }

  getInternetInitialThreshold(): string {
    return this.productInfo.getCharValue(CharsEnum.INTERNET_USAGE_INITIAL_THRESHOLD)[0];
  }

  getInternetAlertThreshold(): string {
    return this.productInfo.getCharValue(CharsEnum.INTERNET_USAGE_ALERT_THRESHOLD)[0];
  }

  getInternetUsageLimit(): string {
    return this.productInfo.getCharValue(CharsEnum.INTERNET_USAGE_LIMIT)[0];
  }
}
